	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Google Tech Talk\nJanuary 15, 2013\n(more info below)\n\nPresented by Douglas Crockford\n\nABSTRACT\n\nThe wonders of monads are finally revealed without resorting to Category Theory or Haskell. It turns out that monads are trivially expressed in JavaScript, and are one of the key enablers of Ajax. Monads are amazing. They are simple things, almost trivially implemented, with enormous power to manage complexity. But understanding them is surprisingly difficult, and most people, once they have that ah-ha moment, seem to lose the ability to explain them to others.","meta":{"duration":"PT49M48S"},"presenter":{"name":null},"source":{"id":"b0EF0VTs9Dc","provider":"youtube"},"slug":"monads-and-gonads","tag":["monads","Google Tech Talk","Douglas Crockford","Javascript","Ajax"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/b0EF0VTs9Dc/default.jpg","height":90,"width":120}},"title":"Monads and Gonads"}}]}},"pathContext":{"slug":"ajax","tag":"Ajax"}}

